html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font: 1.6rem Helvetica;
  height: 100vh;
  margin: 0;
}

h1 {
  font-size: 2rem;
  padding: 2rem;
  margin: 0;
}

input, select, textarea, button {
  padding: 0.5rem;
  border: 0.1rem solid #c0c0c0;
  border-radius: 0.5rem;
  cursor: pointer;
}

#root {
  height: 100%;
}

.grid-container {
  display: grid;
  grid-template-areas: "header" "main" "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}

.header {
  grid-area: header;
  background-color: black;
  color: whitesmoke;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}

.brand a {
  color: white;
  font-size: 2.5rem;
  font-weight: bold;
  text-decoration: none;
  font-family: "Lucida Console", Monaco, monospace;
}

.social-text {
  color: #f5deb3;
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  font-family: "Lucida Console", Monaco, monospace;
}

.header-links a {
  color: white;
  text-decoration: none;
  padding: 0.3rem;
}

.header-links a:hover {
  color: tomato;
}

.main {
  grid-area: main;
}

.footer {
  grid-area: footer;
  background-color: black;
  color: wheat;
  display: flex;
  justify-content: center;
  align-items: center;
}

a {
  text-decoration: none;
}

a:hover {
  color: tomato;
}

/* HomeScreen */

.black-bg {
  margin: auto;
  background-color: black;
}

.center-img {
  display: block;
  margin: auto;
}

.home-filter {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}

.products {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 0;
}

.products li {
  list-style-type: none;
  padding: 0;
  flex: 0 1 34rem;
  margin: 1rem;
  height: 50rem;
  border-bottom: 0.1rem #c0c0c0 solid;
}

.product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.product h2 {
  font-size: 1.8rem;
}

.product-image {
  max-width: 34rem;
  max-height: 34rem;
}

.product-name {
  font-size: 2rem;
}

.product-category {
  font-size: 1.2rem;
  color: #808080;
}

.product-collectionName {
  font-size: 1.2rem;
  color: #808080;
}

.product-price {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.questions {
  width: 100%;
}

/* Sidebar styling */

.brand button {
  font-size: 3rem;
  padding: 0.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.sidebar {
  position: fixed;
  transition: all 0.5s;
  transform: translateX(-30rem);
  width: 30rem;
  background-color: #f0f0f0;
  height: 100%;
}

.sidebar.open {
  transform: translateX(0);
}

.sidebar-close-button {
  border-radius: 50%;
  border: 0.1rem #000000 solid;
  width: 3rem;
  height: 3rem;
  padding: 0.5rem;
  font-size: 2rem;
  padding-top: 0;
  cursor: pointer;
  position: absolute;
  right: 0.5rem;
  top: 1.5rem;
}

/* ProductScreen  */

.details {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1rem;
}

.details-image {
  flex: 2 1 60rem;
}

.details-image img {
  max-width: 60rem;
  widows: 100%;
}

.details-info {
  flex: 1 1 30rem;
}

.details-action {
  flex: 1 1 30rem;
  border: 0.1rem #808080 solid;
  border-radius: 0.5rem;
  background-color: #f8f8f8;
  padding: 1rem;
}

.details-info ul, .details-action ul {
  list-style: none;
  padding: 0;
}

.details-info li, .details-action li {
  margin-bottom: 1rem;
  padding: 0;
}

.back-to-result {
  padding: 1rem;
}

.details-action ul li:last-child {
  display: flex;
  flex-direction: column;
}

.button {
  padding: 0.2rem;
  margin-top: 2rem;
  border: 0.1rem #808080 solid;
  border-radius: 5rem;
  cursor: pointer;
  font-size: 1.6rem;
}

.td {
  padding: 2rem;
}

.full-width {
  width: 100%;
}

.button:hover {
  border: 0.1rem #404040 solid;
}

.button.primary {
  background-color: #f0c040;
}

.button-secondary {
  background-color: #f0f080;
}

.text-center {
  text-align: center;
}

img.small {
  max-height: 5rem;
}

ul.images {
  display: flex;
}

ul.images li {
  margin: 0.5rem;
}

ul.images button {
  background: none;
  cursor: pointer;
}

/* Cart  */

.cart {
  display: flex;
  flex-wrap: wrap;
  margin: 1rem;
  align-items: flex-start;
}

.cart-list {
  flex: 3 1 60rem;
}

.cart-action {
  flex: 1 1 20rem;
  background-color: #f8f8f8;
  border: 0.1rem #808080 solid;
  border-radius: 0.5rem;
  padding: 1rem;
}

.cart-list h3 {
  margin-bottom: 0;
  padding-bottom: 0;
}

.cart-list-container {
  padding: 0;
  list-style-type: none;
  padding: 1rem;
}

.cart-list-container li {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
  border-bottom: 0.1rem #808080 solid;
}

.cart-list-container li img {
  max-width: 10rem;
  max-height: 10rem;
}

.cart-list-container li :first-child {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.inline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 15rem;
}

.inline button {
  padding: 0.5rem;
}

.cart-image {
  flex: 1 1;
}

.cart-name {
  flex: 8 1;
}

.cart-price {
  flex: 1 1;
  font-size: 2.5rem;
  text-align: right;
}

.cart-badge {
  position: absolute;
  background-color: #ff0000;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  margin-left: -16px;
  margin-top: -10px;
  font-size: 13px;
  text-align: center;
  font-weight: bold;
  line-height: 20px;
}

/* LoginForm */

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.form-container {
  display: flex;
  flex-direction: column;
  width: 32rem;
  padding: 2rem;
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem;
  list-style: none;
}

.form-container li {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  margin-top: 1rem;
}

/* /check here */

.input {
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem;
}

/* Products screen */

.product-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.content-margined {
  margin: 1rem;
}

.table {
  width: 100%;
}

th {
  text-align: left;
}

tbody>tr:nth-child(odd) {
  background-color: #f0f0f0;
}

/* Checkout Steps */

.checkout-steps {
  display: flex;
  justify-content: space-between;
  width: 40rem;
  margin: 1rem auto;
}

.checkout-steps>div {
  border-top: 0.3rem #c0c0c0 solid;
  color: #c0c0c0;
  flex: 1 1;
  padding-top: 1rem;
}

.checkout-steps>div.active {
  border-top: 0.3rem #f08000 solid;
  color: #f08000;
}

/* Place Order */

.placeorder {
  display: flex;
  flex-grow: wrap;
  padding: 1rem;
  justify-content: space-between;
}

.placeorder-info {
  flex: 3 1 60rem;
}

.placeorder-action {
  flex: 1 1 20rem;
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem;
  background-color: #fcfcfc;
  padding: 1rem;
}

.placeorder-info>div {
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem;
  background-color: #fcfcfc;
  padding: 1rem;
  margin: 1rem;
}

.placeorder-info>div:first-child {
  margin-top: 0;
}

.placeorder-action>ul {
  padding: 0;
  list-style-type: none;
}

.placeorder-action>ul>li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.placeorder-action>ul>li:last-child {
  font-size: 2rem;
  font-weight: bold;
  color: #c04000;
}

.placeorder-actions-payment>div {
  width: 100%;
}

/* Profile page */

.profile {
  display: flex;
  flex-wrap: wrap;
}

.profile-info {
  flex: 1 1 30rem;
}

.profile-orders {
  flex: 3 1 60rem;
}

/* admin */

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  position: absolute;
  display: none;
  right: 0;
  padding: 1rem;
  list-style-type: none;
  z-index: 1;
  background-color: #203040;
  margin: 0;
  margin-top: 0.2rem;
}

.dropdown-content li {
  padding: 0.3rem;
}

.dropdown:hover .dropdown-content {
  display: block;
}

/* Menu bar */

.menubar {
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.menubar a {
  display: flex;
  padding: 1rem;
}

.menubar a:hover {
  background-color: #c0c0c0;
}

/* Monthly drop */

.monthly-drop {
  text-align: center;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-weight: 100;
}

/* GENERALSTYLING */

.center-textonly {
  text-align: center;
}

.center-text {
  text-align: center;
  font-family: italic;
  font-weight: 100;
}

/* collections and lookbook */

/* .logo {
  background-image: url(../public/images/2.png);
} */

.bgimg {
  /* Background image */
  /* background-image: url('../public/images/2.png'); */
  /* Full-screen */
  height: 100%;
  /* Center the background image */
  background-position: center;
  /* Scale and zoom in the image */
  background-size: cover;
  /* Add position: relative to enable absolutely positioned elements inside the image (place text)
  position: relative; */
  /* Add a white text color to all elements inside the .bgimg container */
  color: white;
  /* Add a font */
  font-family: "Courier New", Courier, monospace;
  /* Set the font-size to 25 pixels */
  font-size: 55px;
}

/* Position text in the top-left corner */

.topleft {
  position: absolute;
  top: 0;
  left: 16px;
}

/* Position text in the bottom-left corner */

.bottomleft {
  position: absolute;
  bottom: 0;
  left: 16px;
  align-content: center;
}